.SuperCheckbox_spanClassName__2SSWM {
    position: relative;
    padding: 0 0 0 60px;
    cursor: pointer;
}

.SuperCheckbox_spanClassName__2SSWM:before {
    content: '';
    position: absolute;
    top: -4px;
    left: 0;
    width: 50px;
    height: 26px;
    border-radius: 13px;
    background: #CDD1DA;
    box-shadow: inset 0 2px 3px rgba(0,0,0,.2);
    transition: .2s;
}
.SuperCheckbox_spanClassName__2SSWM:after {
    content: '';
    position: absolute;
    top: -2px;
    left: 2px;
    width: 22px;
    height: 22px;
    border-radius: 10px;
    background: #FFF;
    box-shadow: 0 2px 5px rgba(0,0,0,.3);
    transition: .2s;
}
.SuperCheckbox_checkbox__g1cBY:checked + .SuperCheckbox_spanClassName__2SSWM:before {
    background: #9FD468;
}
.SuperCheckbox_checkbox__g1cBY:checked + .SuperCheckbox_spanClassName__2SSWM:after {
    left: 26px;
}
.SuperCheckbox_checkbox__g1cBY:focus + .SuperCheckbox_spanClassName__2SSWM:before {
    box-shadow: inset 0 2px 3px rgba(0,0,0,.2), 0 0 0 3px rgba(255,255,0,.7);
}
